import {
  createRouter,
  createWebHistory,
  RouteRecordRaw
  // createWebHashHistory
} from 'vue-router'

const routes: Array<RouteRecordRaw> = [
  {
    path: '/login',
    name: 'login',
    component: () => import('views/login/src/Login.vue')
  },
  {
    path: '/',
    name: 'main',
    redirect: '/user-info',
    component: () => import('views/main/structure/src/Structure.vue'),
    children: [
      {
        path: '/user-info',
        name: 'userInfo',
        component: () => import('views/main/user-info/src/UserInfo.vue')
      },
      {
        path: '/user-train-info',
        name: 'userTrainInfo',
        component: () =>
          import('views/main/user-train-info/src/UserTrainInfo.vue')
      },
      {
        path: '/team-train-info',
        name: 'teamTrainInfo',
        component: () =>
          import('views/main/team-train-info/src/TeamTrainInfo.vue'),
        meta: { requiresAuth: true }
      },
      {
        path: '/sign-in-out',
        name: 'signInOut',
        component: () => import('views/main/sign-in-out/src/SignInOut.vue'),
        meta: { requiresAuth: true }
      }
    ]
  },
  {
    path: '/404',
    name: 'page-not-found',
    component: () => import('views/page-not-found/src/PageNotFound.vue')
  },
  {
    path: '/:pathMatch(.*)',
    redirect: '/404'
  }
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  // history: createWebHashHistory(process.env.BASE_URL),
  routes
})

router.beforeEach((to) => {
  const login = window.sessionStorage.getItem('login')
  const admin = window.sessionStorage.getItem('admin')
  // 未登录访问网站，跳转登录页
  if (to.name != 'login' && login != 'true') {
    console.log('未登录，重定向到登录页面')
    return '/login'
  }
  // 未授权访问特殊页面，跳转404
  if (to.meta.requiresAuth && admin != 'true') {
    console.log('未授权或未定义路由，页面访问失败')
    return '/404'
  }
})

export default router
