import { createStore } from 'vuex'

export default createStore({
  state: {
    theme: 'default',
    collapse: false
  },
  mutations: {
    changeTheme(state) {
      state.theme == 'default'
        ? (state.theme = 'dark')
        : (state.theme = 'default')
    },
    collapseNav(state) {
      state.collapse = !state.collapse
    }
  },
  actions: {},
  modules: {}
})
