import { App } from 'vue'

// 导入element-plus的样式
import 'element-plus/dist/index.css'
// 按需导入element-plus的组件
import {
  ElButton,
  ElInput,
  ElForm,
  ElFormItem,
  ElContainer,
  ElAside,
  ElHeader,
  ElFooter,
  ElMain,
  ElMenu,
  ElMenuItem,
  ElDescriptions,
  ElDescriptionsItem,
  ElTable,
  ElTableColumn,
  ElSkeleton,
  ElSkeletonItem,
  ElSelect,
  ElOption
} from 'element-plus'

const components = [
  ElButton,
  ElInput,
  ElForm,
  ElFormItem,
  ElContainer,
  ElAside,
  ElHeader,
  ElFooter,
  ElMain,
  ElMenu,
  ElMenuItem,
  ElDescriptions,
  ElDescriptionsItem,
  ElTable,
  ElTableColumn,
  ElSkeleton,
  ElSkeletonItem,
  ElSelect,
  ElOption
]

export default function (app: App): void {
  for (const component of components) {
    app.component(component.name, component)
  }
}
